
.left-panel-main{
  width: 15%;
  background-color: #444;
  float: left;
  height: 100vh;
  position: fixed;
}
.nav-li{
  color: white;
  text-align: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 800;
  border-bottom: 2px solid white;

  -webkit-transition: 1s;

  -o-transition: 1s;

  transition: 1s;
}
.nav-li-top{
  border-top: 2px solid white;
}
.nav-container{
  margin-top: 90%;
}
.nav-li:hover{
  cursor: pointer;
}
.clickable:hover{
  background-color: rgba(255,255,255,.5)
}
.nav-pers,
.nav-prof{
  font-size: 12px;
  font-weight: normal;
  padding: 3px;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.nav-pers-container:hover .nav-pers{
  display:block;
}
.nav-prof-container:hover .nav-prof{
  display: block;
}
.content-container{
  padding: 20px;
  margin-top: 20vh;
  margin-bottom: 20vh;
  display: inline-block;
  width: 70%;
  margin-left: auto;
  
}
.right-panel-main{
  display: inline-block;
  margin-left: 15%;
  width: 70%;
  text-align: center;
}
.rp-bac{
  width: 100%;
  height: 100vh;
  position: fixed;
  top:0;
  left:15%;
  z-index: -100;
  background-size: contain;
  background-image: url('./images/profback.png');
 background-repeat: repeat-y;
}
.pic-div{
  float: left;  
  height: 150px;
  width: 300px;
  background-size:cover;
}
.list-stuff{
  display: inline-block;
}
.content-title{
  font-size: 35px;
  margin-bottom: 5px;
  font-weight: 800;
  color: #444;
  text-decoration: underline;
}


.clearfix::after {
    content: "";
    clear: both;
    display: table;
}



